.contact-div-main {
    display: flex;
  }
  
  .dark-mode a {
    color: #123456; /* Replace with actual $textColorDark */
  }
  
  .contact-div-main > * {
    flex: 1;
  }
  
  .contact-title {
    font-size: 65px;
    font-weight: 400;
  }
  
  .contact-detail,
  .contact-detail-email {
    margin-top: 20px;
    font-size: 40px;
    font-weight: 400;
    color: #888888; /* Replace with actual $subTitle */
    text-decoration: none;
  }
  
  .contact-detail:hover,
  .contact-detail-email:hover {
    color: #123456; /* Replace with actual $textColor */
    text-shadow: 2px 1px 2px #999999; /* Replace with actual $contactDetailHoverTS */
    transition: all 0.3s;
  }
  
  .contact-subtitle {
    text-transform: uppercase;
  }
  
  .contact-text-div {
    margin-top: 1.5rem;
  }
  
  .contact-margin-top {
    margin-top: 4rem;
  }
  
  .contact-image-div > img {
    max-width: 100%;
    height: auto;
    margin-left: 1.5rem;
    margin-top: -4rem;
  }
  
  /* Media Queries */
  @media (max-width: 1380px), (max-width: 768px) {
    .contact-title {
      font-size: 40px;
      font-weight: 400;
      text-align: center;
    }
  
    .contact-subtitle {
      font-size: 16px;
      line-height: normal;
      text-align: center;
    }
  
    .contact-text-div {
      text-align: center;
      margin-top: 1.5rem;
    }
  
    .contact-detail,
    .contact-detail-email {
      font-size: 20px;
    }
  
    .contact-image-div {
      display: none;
    }
  }